import React, {useState} from 'react';
import axios from 'axios';
import '../style/style.scss';
import Alert from '../hooks/alert';
import QueryString from 'querystring';

//
const Login = (props) => {
    const {close} = props;  //  

    const [alertModal, setAlertModal] = useState(false); //   .
    const [alertValue, setAlertValue] = useState(''); //    .

    const [joinModal, setJoinModal] = useState(false); //  &

    const [email, setEmail]=useState('');
    const [pw, setPw]=useState('');

    const emailRegEx = /^[A-Za-z0-9]([-_.]?[A-Za-z0-9])*@[A-Za-z0-9]([-_.]?[A-Za-z0-9])*\.[A-Za-z]{2,3}$/;
    const [formCheck, setFormCheck] = useState({
        email: false, 
        pw: false,
    });

    let sessionStorage = window.sessionStorage; // .

    const LoginCheck = () => {
        if(formCheck.email === false){ //   
            setAlertModal(true);
            setAlertValue(' .');
        }else if(formCheck.pw === false){ //    
            setAlertModal(true);
            setAlertValue('  .');
        }else{ //   
            axios({
                method:'post', //method:   
                url:'https://tjddus0630.cafe24.com/sy_memo/ajax/login.php',
                data:QueryString.stringify({email:email, pw:pw}),
            })
            .then(res => res.data)
            .then(data => {
                if(data.result === true){ // 
                    close(true);
                    sessionStorage.setItem("loginName", data.name); //    
                    sessionStorage.setItem("loginEmail", data.email); //    
                }else{ //     
                    setAlertModal(true);
                    setAlertValue(data.msg);
                }
            }) // 
        }
    }

    return(
        <div className='window'>
            <div className='loginWindow'>
                <h1>LOGIN</h1>
                <form id='LoginForm' method='post'>
                    <input type='text' name='email' placeholder='ID (Email)' value={email}
                        onChange={(e)=>{setEmail(e.target.value)
                            if(emailRegEx.test(email)) setFormCheck({...formCheck, email:true})
                            else setFormCheck({...formCheck, email:false})
                        }}/>
                    <input type='password' name='pw' placeholder='Password' value={pw}
                        onChange={(e)=>{setPw(e.target.value)
                            if(e.target.value !== '') setFormCheck({...formCheck, pw:true})
                            else setFormCheck({...formCheck, pw:false})
                        }}/>
                    <label>
                        <input type='checkbox' id='remember-id'/>   
                    </label>
                </form>
                <button className='loginBtn' type='submit' style={{float:'left'}} onClick={()=>LoginCheck()}></button>
                <button className='loginBtn' type='button' style={{float:'right'}} onClick={()=>{setJoinModal(true)}}></button>
                {joinModal === true? <Join close={setJoinModal} /> : null}
                {alertModal === true? <Alert close={setAlertModal} value={alertValue}/> : null}
            </div>
        </div>  
    );
};

//
const Join = (props) => {

    const {close} = props; // 
    const [alertModal, setAlertModal] = useState(false); //  .
    const [alertValue, setAlertValue] = useState('');

    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [pw, setPw] = useState(''); //
    const [pwCheck, setPwCheck] = useState('');
    

    // 
    const nameRegEx = /^[-]+$/;
    const emailRegEx = /^[A-Za-z0-9]([-_.]?[A-Za-z0-9])*@[A-Za-z0-9]([-_.]?[A-Za-z0-9])*\.[A-Za-z]{2,3}$/;
	const pwRegEx = /^[A-Za-z0-9]{7,20}$/;

    const [formCheck, setFormCheck] = useState({
        name:false,
        email: false, 
        pw: false,
        pwCheck: false,
    });

    const JoinAccess = () => { //     
       
        if(formCheck.name && formCheck.email && formCheck.pw && formCheck.pwCheck){
            axios({
                method:'post', //method:   
                url:'https://tjddus0630.cafe24.com/sy_memo/ajax/join_update.php',
                data:QueryString.stringify({name:name, email:email, pw:pw}),
            })
            .then(res => res.data)
            .then(data => {
                if(data){
                    setAlertModal(true);
                    setAlertValue(data);
                }
                else{
                    close(false);
                }
            }) // 
        }else if(!formCheck.name){
            setAlertModal(true);
            setAlertValue('   .');
        }else if(!formCheck.email){
            setAlertModal(true);
            setAlertValue('   .');
        }else if(!formCheck.pw){
            setAlertModal(true);
            setAlertValue('   .');
        }else{
            setAlertModal(true);
            setAlertValue('  .');
        }
        
    }

    return(
        <div className='window'>
            <div className='joinWindow'>
                <h1></h1>
                <form method='POST' id='LoginForm' >
                    <input type='text' name='name' placeholder='' value={name} 
                        onChange={(e)=>{setName(e.target.value)
                            if(nameRegEx.test(name)){setFormCheck({...formCheck, name:true})}
                            else setFormCheck({...formCheck, name:false})
                        }}/>
                    <input type='text' name='email' placeholder='ID (Email)' value={email} 
                        onChange={(e)=>{setEmail(e.target.value)
                            if(emailRegEx.test(email)) {setFormCheck({...formCheck, email:true})}
                            else setFormCheck({...formCheck, email:false})
                        }}/>
                    <input type='password' name='pw' placeholder='' value={pw}
                        onChange={(e)=>{setPw(e.target.value)
                            if(pwRegEx.test(pw)) {setFormCheck({...formCheck, pw:true})}
                            else {setFormCheck({...formCheck, pw:false})}
                        }}/> 
                    <p> *   ,   8~20 .</p>
                    <input type='password' name='pwcheck' placeholder=' ' 
                        onChange={(e)=>{setPwCheck(e.target.value)
                            if(pw===e.target.value) {setFormCheck({...formCheck, pwCheck:true})}
                            else setFormCheck({...formCheck, pwCheck:false})
                         }}/>
                </form>
                <button type='submit' className='joinBtn' style={{float:'left'}} onClick={() => JoinAccess()}></button>
                <button className='joinBtn' style={{float:'right'}} onClick={()=>{close(false)}}></button>
                {alertModal === true? <Alert close={setAlertModal} value={alertValue}/> : null}
            </div>
        </div>
    );
};

export default Login;