import React, {useState} from 'react';
import '../style/style.scss';
import Alert from '../hooks/alert';

const Lock = (props) => { //  
    
    const [alertModal, setAlertModal] = useState(false); // 4    

    const [pw, setPw] = useState('');
    const pwRegEx = /^[0-9]{4}$/; //  4 

    const handler = () => {
        if(pwRegEx.test(pw)){ // 4  .
            props.pw(pw);
            props.close(false);
        }else{ //  
            setAlertModal(true);
        }
    }

    return(
        <div className='alert lock'>
            <img src={process.env.PUBLIC_URL+'/images/lock.png'} alt='lock_img' className='logoImg'/>
            <p style={{whiteSpace: 'pre-wrap'}}>( 4) .</p>
            <input style={{textAlign:'center'}} type='password' className='input' onChange={(e) => setPw(e.target.value)} value={pw}/>
            <br/><button onClick={()=>handler()}></button>


            {alertModal === true? <Alert close={setAlertModal} value={' 4 .'}
            styleWindow={{position: 'fixed'}} styleAlert={{height: '70%'}}/> : null}


        </div>
    )
}

export default Lock;