import React, {useEffect, useState} from 'react';
import '../style/style.scss';
import Cancle from '../hooks/cancle';
import Choice from '../hooks/choice';
import Alert2 from '../hooks/alert2';
import Lock from '../hooks/lock';
import axios from 'axios';
import QueryString from 'querystring';


const Write = () => {

    const [alert, setAlert] = useState(false);  // 
    const [cancleModal, setCancleModal] = useState(false); // 
    const [choice, setChoice] = useState(false); // 

    const [alertValue, setAlertValue] = useState(''); // 
    const [alertImg, setAlertImg] = useState(''); //  
    

    const[title, setTitle] = useState(''); // 
    const [content, setContent] = useState('');  // 
    const [pw, setPw] = useState(''); //

    
    const [lock, setLock] = useState(''); //  
    const [lockOpen, setLockOpen] = useState(false);
    

    useEffect(() => {
        
        if(lock === 'no') Save();
        else if( lock === 'yes' && pw === '') openlock(); 
        else if(lock === 'yes' && pw !== '') Save();

        
    }, [lock, pw]);

    const SaveCheck = () => { //,   

        if( title !== '' && content !== ''){ //,     
            setChoice(true);
        }else if(title === ''){ // 
            setAlert(true);
            setAlertValue(' .');
            setAlertImg('/images/alert.png');
        }else{ // 
            setAlert(true);
            setAlertValue(' .');
            setAlertImg('/images/alert.png');
        }
    }

    function openlock(){
        setLockOpen(true);
    }


    const Save = () => { //

       axios({
                method:'post', //method:   
                url:'https://tjddus0630.cafe24.com/sy_memo/ajax/text_save.php',
                data:QueryString.stringify({email:sessionStorage.getItem("loginEmail"), title:title, content:content, lock:pw}),
            })
        .then(res => res.data)
        .then(data => {
            if(data){ // 
                setAlert(true);
                setAlertValue(data);
                setAlertImg('/images/confirmed.png');
            }
            else{ // 
                setAlert(true);
                setAlertValue(' .');
                setAlertImg('/images/alert.png');
            }
        })
    }


    return(
        <div className='Content'>
            <div className='memo'>
                <form>
                    <label></label>
                        <input type='text' className='input' placeholder='  .'
                        onChange={(e)=>{setTitle(e.target.value)}}/>
                    <label></label> 
                        <textarea className='inputText' placeholder='  .'
                        onChange={(e) => {setContent(e.target.value)}}/>
                    <button type='button' onClick={()=>{SaveCheck();}}></button>
                    <button type='button' onClick={()=>{setCancleModal(true); setAlertValue('   .\n  ?')} }></button>
                </form>
            </div>
            {cancleModal===true? <Cancle close={setCancleModal} value={alertValue}/> : null} 
            {alert === true? <Alert2 close={setAlert} value={alertValue} img={alertImg}/> : null}
            {choice === true? <Choice close={setChoice} value={'   ?'} lock={setLock}/> : null}
            {lockOpen === true? <Lock close={setLockOpen} pw={setPw}/> : null}
        </div>
    )
}

export default Write;